.projects-container {
  height: 90vh;
  width: 90vw;
  background-color: rgb(238, 238, 238);
  display: flex;
  flex-direction: row;
  margin: 2.5% 0;
  padding: 0% 0% 0% 0%;
}
.project-section-title {
  width: 15%;
  height: 100%;
  padding: 3% 6%;
  text-transform: uppercase;
  border-left: 5px solid grey;
  font-size: 3.6rem;
  color: white;
  font-weight: 800;
  word-wrap: break-word;
  line-height: 1.27em;
  background-color: black;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.project-cards {
  margin: 4% 2%;
  overflow-y: scroll;
}

.project-cards::-webkit-scrollbar {
  width: 2px; /* width of the entire scrollbar */
}
.project-cards::-webkit-scrollbar-track {
  background: grey; /* color of the tracking area */
}
.project-cards::-webkit-scrollbar-thumb {
  background-color: black; /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 5px solid black; /* creates padding around scroll thumb */
}
